import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import * as appGlobals from '../app.globals';
import {Observable} from 'rxjs/Observable';
import {ProtokollToElisaRequest} from '../model/ProtokollToElisaRequest';
import {ElisaData} from '../model/ElisaData';
import {ElisaTitleData} from '../model/ElisaTitleData';
import {ElisaTitle} from '../model/ElisaTitle';
import {PrimoData} from '../model/PrimoData';

@Injectable({
  providedIn: 'root',
})
export class ElisaService {

  public elisaData: ElisaData[];

  public elisaTitleData: ElisaTitleData;

  public notepad = 'Ausleihprotokoll';

  private notizIntern = 'X Exemplare aus Etat ';

  constructor(private http: HttpClient) {
  }

  createElisaRequest(isbn: string, shortShelfmarks: string) {
    const libraryNote = 'E/D??:1, NB ' + shortShelfmarks;
    this.elisaTitleData = new ElisaTitleData(this.notizIntern, libraryNote, isbn);
  }

  sendToElisa(): Observable<string> {
    const titles = [];
    titles.push(new ElisaTitle(this.elisaTitleData));
    const protokollToElisaRequest = new ProtokollToElisaRequest(this.notepad, this.elisaData[0].elisaUserId, titles);
    return this.http.post<string>(appGlobals.elisaUrl, JSON.stringify(protokollToElisaRequest), {headers: appGlobals.headersJson});
  }

  setElisaData(username: string): Observable<ElisaData[]> {
    const url = appGlobals.settingsUrl + '/elisadata/forLibintel/' + username;
    // const url = '/assets/data/elisa_data_example.json';
    const observable: Observable<ElisaData[]> = this.http.get<ElisaData[]>(url);
    observable.subscribe(value => this.elisaData = value);
    return observable;
  }
}
