import {Component, OnInit} from '@angular/core';
import {ProtokollRequest} from './model/ProtokollRequest';
import {GetterService} from './service/getter.service';
import {Message} from 'primeng/primeng';

import {ActivatedRoute, Params, Router} from '@angular/router';
import 'rxjs/add/operator/switchMap';
import {MenuItem, MessageService} from 'primeng/api';
import {TranslateService} from './translate';
import {ClipboardService} from 'ngx-clipboard';
import {PrimoResponse} from './model/PrimoResponse';
import {AuthenticationService} from './service/authentication.service';
import {Principal} from './model/Principal';
import {ElisaTitleData} from './model/ElisaTitleData';
import {ElisaService} from './service/elisa.service';
import {ElisaTitle} from './model/ElisaTitle';
import {ProtokollToElisaRequest} from './model/ProtokollToElisaRequest';
import {ElisaData} from './model/ElisaData';
import {FilterService} from './service/filter.service';
import {DataService} from './service/data.service';
import {MemorylistService} from './service/memorylist.service';
import {MemoryListItem} from './model/MemoryListItem';


@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  providers: [GetterService]
})
export class AppComponent implements OnInit {
  constructor(public getterService: GetterService,
              private router: Router,
              private route: ActivatedRoute,
              private translateService: TranslateService,
              private clipboardService: ClipboardService,
              private messageService: MessageService,
              public  authenticationService: AuthenticationService,
              public elisaService: ElisaService,
              public filterService: FilterService,
              public dataService: DataService,
              public itemlistService: MemorylistService) {
  }


  recKeys: Set<string>;

  title: string;

  busy: boolean;

  primaryLoad = true;

  messages: Message[];

  protokollRequest: ProtokollRequest;

  items: MenuItem[] = [];

  activeItem: MenuItem;

  activePart: string;

  status = 'ready';

  principal: Principal;


  response = '';

  isFachreferent = false;

  private tabs: string[] = ['overview', 'bibliography', 'statistics', 'usage', 'itemlist', 'eventlist'];

  ngOnInit(): void {
    this.itemlistService.memorylistItemChanged$.subscribe(
      savedItem => this.collectSaveditem()
    );
    this.filterService.filterChanged$.subscribe(change => this.updateData());
    this.filterService.show['elisa'] = false;
    this.authenticationService.updatePrincipal().subscribe(
      data => {
        this.principal = data;
        if (this.principal.roles) {
          this.isFachreferent = this.principal.roles.indexOf('ROLE_FACHREFERENT') > -1;
          if (this.isFachreferent) {
            this.elisaService.setElisaData(this.principal.name).subscribe(
              value => {
                this.filterService.show['elisaData'] = true;
              },
              error => {
                console.log('no elisa data found');
                this.filterService.show['elisaData'] = false;
              }
            );
            this.tabs.push('analysis');
            this.buildTabs();
          }
        }
      },
      error => console.log(error)
    );
    this.translateService.use('de');
    this.buildTabs();
    this.activeItem = this.items[0];
    this.activePart = this.activeItem.id;
    this.protokollRequest = new ProtokollRequest();
    this.route.queryParams.subscribe((params: Params) => {
      if (params['shelfmark'] !== undefined) {
        this.protokollRequest.shelfmark = params['shelfmark'];
        this.protokollRequest.update();
        if (params['exact'] !== undefined) {
          this.protokollRequest.exact = ('true' === params['exact']);
        }
        if (params['collections'] !== undefined) {
          this.protokollRequest.collections = params['collections'];
        }
        if (params['materials'] !== undefined) {
          this.protokollRequest.shelfmark = params['materials'];
        }
        this.collect();
      }
    });
  }

  buildTabs() {
    this.items = [];
    this.tabs.forEach(entry => {
      return this.items.push({
        label: this.translateService.instant('tab.' + entry),
        icon: 'fa-plus',
        id: entry,
        command: event => this.router.navigate(['/' + entry])
      });
    });
  }

  reset() {
    this.getterService.reset();
    this.filterService.resetFilter();
    this.filterService.resetPrintSelection();
    this.filterService.resetDigitalSelection();
    this.recKeys = new Set<string>();
    this.messages = [];
    this.activePart = 'overview';
    this.filterService.show['filter'] = true;
  }

  collect() {
    this.busy = true;
    this.status = 'collecting';
    this.reset();
    this.protokollRequest.update();
    if (this.protokollRequest.getType() === 'ebook') {
      this.getDigitalManifestations();
    } else {
      this.getAllPrintManifestations();
    }
  }

  collectSaveditem() {
    this.reset();
    this.protokollRequest.shelfmark = this.itemlistService.activeMemorylistItem.shelfmark;
    this.collect();
  }

  getAllPrintManifestations() {
    this.getterService.getAllPrintManifestations(this.protokollRequest).subscribe(
      data => {
        if (data.length === 0) {
          this.sendMessage('error', 'nothingFound');
        } else {
          data.forEach(entry => {
            this.getterService.printManifestations.set(entry.titleID, entry);
            const recKey = entry.bibliographicInformation.recKey;
            if ('' !== recKey) {
              if (!this.recKeys.has(recKey)) {
                this.recKeys.add(recKey);
              }
            }
          });
        }
        this.primaryLoad = false;
      },
      error => {
        this.sendMessage('error', 'error');
        this.primaryLoad = false;
        console.log(error);
      },
      () => {
        this.filterService.buildPrintFilterlist(this.getterService.printManifestations);
        if (!(typeof this.protokollRequest.collections === 'undefined' || this.protokollRequest.collections.trim() === '')) {
          this.filterService.includeSelectionFromHttpParamters(this.protokollRequest.collections);
        }
        this.filterService.applyPrintFilter(this.getterService.printManifestations);
        this.collectPrimoResponses();
        this.busy = false;
      }
    );
  }

  getDigitalManifestations() {
    this.getterService.getAllDigitalManifestations(this.protokollRequest).subscribe(
      data => {
        if (data.length === 0) {
          this.sendMessage('error', 'nothingFound');
        } else {
          data.forEach(record => {
            this.getterService.digitalManifestations.set(record.identifier, record);
            this.filterService.filterList[record.identifier] = true;
          });
        }
        this.primaryLoad = false;
      },
      error => {
        this.sendMessage('error', 'error');
        this.primaryLoad = false;
        console.log(error);
      },
      () => {
        this.filterService.buildDigitalFilterlist(this.getterService.digitalManifestations);
        this.filterService.applyDigitalFilter(this.getterService.digitalManifestations);
        this.collectPrimoResponses();
        this.busy = false;
      }
    );
  }

  collectPrimoResponses() {
    this.getterService.printManifestations.forEach(
      (value, key) => {
        if (!this.getterService.primoData.has(key)) {
          let isbn = value.bibliographicInformation.isbn;
          if ('' !== isbn) {
            isbn = isbn.replace('ISBN', '').trim();
            isbn = isbn.replace(/-/g, '').trim();
            if (isbn.length > 13) {
              if (isbn.startsWith('978')) {
                isbn = isbn.substring(0, 13);
              } else {
                isbn = isbn.substring(0, 10);
              }
            }
          }
          if (isbn.length === 13) {
            this.getterService.searchPrimoForIsbn(isbn).subscribe(
              data => this.storePrimoResponse(data),
              error => console.log(error),
              () => this.extendManifestations()
            );
          } else {
            this.getterService.searchPrimoForShelfmark(value.shelfmark).subscribe(
              data => this.storePrimoResponse(data),
              error => console.log(error),
              () => this.extendManifestations()
            );
          }
        }
      });
    this.getterService.digitalManifestations.forEach(
      (value, key) => {
        if (!this.getterService.primoData.has(key)) {
          this.getterService.searchPrimoForIsbn(key).subscribe(
            data => this.storePrimoResponse(data),
            error => console.log(error),
            () => this.extendManifestations()
          );
        }
      });
  }

  storePrimoResponse(data: PrimoResponse) {
    data.electronic.forEach(
      entry => {
        if (!this.getterService.primoData.has(entry.isbn)) {
          this.getterService.primoData.set(entry.isbn, entry);
        }
      }
    );
    data.print.forEach(
      entry => {
        if (!this.getterService.primoData.has(entry.recordId)) {
          this.getterService.primoData.set(entry.recordId, entry);
        }
      }
    );
  }

  extendManifestations() {
    this.getterService.primoData.forEach((value, key) => {
        if ('Online Resource' === value.type) {
          if (!this.getterService.digitalManifestations.has(key)) {
            let collected = false;
            for (const keyInd of key.split(';')) {
              if (keyInd.trim() !== '') {
                if (!collected) {
                  if (value.doi !== '') {
                    this.getterService.getDigitalManifestationByIdentifiers(keyInd, value.doi).subscribe(
                      dm => {
                        if (dm.usage.length > 0) {
                          this.filterService.addDigitalManifestationToFilterlist(dm);
                          this.getterService.digitalManifestations.set(key, dm);
                          collected = true;
                          this.updateData();
                        } else {
                          this.sendMessage('warn', 'noUsage');
                        }
                      }, error => this.sendMessage('error', 'noConnection'));
                  } else {
                    this.getterService.getDigitalManifestation(key).subscribe(
                      dm => {
                        if (dm.usage.length > 0) {
                          this.filterService.addDigitalManifestationToFilterlist(dm);
                          this.getterService.digitalManifestations.set(keyInd, dm);
                          collected = true;
                          this.updateData();
                        } else {
                          this.sendMessage('warn', 'noUsage');
                        }
                      }, error => this.sendMessage('error', 'noConnection')
                    );
                  }
                }
              }
            }
          }
        } else if ('Physical Item' === value.type) {
          if (!this.recKeys.has(key)) {
            let shelfmark = value.shelfmarks;
            if (value.shelfmarks.indexOf(';') > 0) {
              shelfmark = shelfmark.substring(0, shelfmark.indexOf(';'));
            }
            shelfmark = shelfmark.substring(shelfmark.indexOf(' '), shelfmark.length);
            const protokollrequest = new ProtokollRequest();
            protokollrequest.shelfmark = shelfmark;
            protokollrequest.exact = true;
            this.getterService.getAllPrintManifestations(protokollrequest).subscribe(
              pm => {
                pm.forEach(entry => {
                    this.getterService.printManifestations.set(entry.titleID, entry);
                    this.filterService.addPrintManifestationToFilterlist(entry);
                    const recKey = entry.bibliographicInformation.recKey;
                    if ('' !== recKey) {
                      if (!this.recKeys.has(recKey)) {
                        this.recKeys.add(recKey);
                      }
                    }
                  }
                );
              },
              error => this.sendMessage('warn', 'noUsage')
            );
          }
        }
      }
    );
  }

  updateData() {
    if (this.filterService.selectedEvents) {
      this.dataService.generatePrintData(this.filterService.selectedPrintManifestations, this.filterService.selectedEvents);
    }
    if (this.filterService.selectedDigitalUsage) {
      this.dataService.generateDigitalData(this.filterService.selectedDigitalManifestations);
    }
  }

  copyLink() {
    const url = location.href.split('?')[0] + '?' + this.protokollRequest.asUrlParamters();
    this.clipboardService.copyFromContent(url);
    this.messageService.add({
      severity: 'success',
      summary: 'Link kopiert',
      detail: 'Der Link wurde in die Ziwschenablage eingefgt'
    });
  }

  sendMessage(level: string, error: string) {
    this.messages = [];
    switch (level) {
      case 'error': {
        this.messages.push({
          severity: error, summary: 'Fehler: ',
          detail: this.translateService.instant('message.' + error)
        });
      }
    }
    this.status = level;
    this.busy = false;
  }

  sendToElisa() {
    this.messages = [];
    let message = {};
    this.elisaService.sendToElisa().subscribe(
      data => {
        this.response = data;
        message = this.messages.push({
          severity: 'success',
          summary: 'Erfolg: ',
          detail: this.translateService.instant('message.elisa.submitted')
        });
      },
      error => this.messages.push({
        severity: 'error',
        summary: 'Fehler: ',
        detail: this.translateService.instant('no.elisa.connection')
      })
    );
    this.messages.push(message);
    this.filterService.show['elisa'] = false;
  }
}
