import {Injectable} from '@angular/core';
import 'rxjs/add/operator/map';
import {HttpClient} from '@angular/common/http';
import {Principal} from '../model/Principal';
import {Observable} from 'rxjs/Observable';

@Injectable()
export class AuthenticationService {

  public principal: Principal;

  constructor(private http: HttpClient) {
  }

  updatePrincipal(): Observable<Principal> {
    const observable = this.http.get<Principal>(
      '/activeuser'
      // '/assets/data/example_authentication.json'
    );
    observable.subscribe( value => this.principal = value);
    return observable;
  }

  logout(): void {
    this.http.post('/logout', {}, {responseType: 'text'}).subscribe(
      () => this.principal = null
    );
  }
}
