import {EventEmitter, Injectable} from '@angular/core';
import {PrintManifestation} from '../model/PrintManifestation';
import {DigitalManifestation} from '../model/DigitalManifestation';
import {Event} from '../model/Event';
import {Item} from '../model/Item';
import {EbookCounter} from '../model/EbookCounter';

@Injectable({
  providedIn: 'root',
})
export class FilterService {

  public show: object;

  filterList: object;
  selectedPrintManifestations: PrintManifestation[];
  selectedDigitalManifestations: DigitalManifestation[];
  selectedEvents: Event[];
  selectedItems: Item[];
  public uniqueCollections: string[];
  public uniqueMaterials: string[];
  private uniqueCollectionsSet: Set<string>;
  private uniqueMaterialsSet: Set<string>;
  private filteredItems: Map<string, Item[]>;
  private filteredEvents: Map<string, Event[]>;
  public filteredManifestations: Map<string, PrintManifestation>;
  public selectedDigitalUsage: Map<string, EbookCounter[]>;
  public selectedShelfmarks: Set<string>;
  public selectedCollections: Set<string>;
  statsCollection: Map<string, number>;
  statsMaterials: Map<string, number>;
  statsManifestations: Map<string, number>;

  public filterChanged$: EventEmitter<string>;

  constructor() {
    this.filterChanged$ = new EventEmitter();
    this.resetFilter();
  }

  resetFilter() {
    this.show = {};
    this.filterList = {};
    this.uniqueCollections = [];
    this.uniqueMaterials = [];
    this.uniqueCollectionsSet = new Set<string>();
    this.uniqueMaterialsSet = new Set<string>();
  }

  resetPrintSelection() {
    this.selectedPrintManifestations = [];
    this.selectedEvents = [];
    this.selectedItems = [];
    this.filteredManifestations = new Map<string, PrintManifestation>();
    this.filteredItems = new Map<string, Item[]>();
    this.filteredEvents = new Map<string, Event[]>();
    this.statsCollection = new Map<string, number>();
    this.statsMaterials = new Map<string, number>();
    this.statsManifestations = new Map<string, number>();
    this.selectedCollections = new Set<string>();
    this.selectedShelfmarks = new Set<string>();
  }

  resetDigitalSelection() {
    this.selectedDigitalManifestations = [];
    this.selectedDigitalUsage = new Map<string, EbookCounter[]>();
  }

  buildPrintFilterlist(printManifestations: Map<string, PrintManifestation>) {
    this.resetFilter();
    if (printManifestations && printManifestations.size > 0) {
      this.show['editions'] = true;
      this.show['materials'] = true;
      this.show['collections'] = true;
      this.show['filter'] = true;
      this.show['print'] = true;
      printManifestations.forEach(value => {
        this.filterList[value.titleID] = true;
        value.collections.forEach(collection => {
          if (!this.uniqueCollectionsSet.has(collection)) {
            this.uniqueCollectionsSet.add(collection);
            this.filterList[collection] = (collection !== '???');
          }
        });
        value.materials.forEach(material => {
          if (!this.uniqueMaterialsSet.has(material)) {
            this.uniqueMaterialsSet.add(material);
            this.filterList[material] = true;
          }
        });
      });
      this.uniqueMaterials = Array.from(this.uniqueMaterialsSet).sort();
      this.uniqueCollections = Array.from(this.uniqueCollectionsSet).sort();
    }
  }

  buildDigitalFilterlist(digitalManifestations: Map<string, DigitalManifestation>) {
    if (digitalManifestations && digitalManifestations.size > 0) {
      digitalManifestations.forEach((value) => this.filterList[value.identifier] = true);
      this.show['digital'] = true;
      this.show['filter'] = true;
    }
  }

  includeSelectionFromHttpParamters(collections: string) {
    let individualCollections: string[] = [];
    if (collections.indexOf(' ') > -1) {
      individualCollections = collections.split(' ');
    } else {
      individualCollections.push(collections);
    }
    for (const f of this.uniqueCollections) {
      let fitting = false;
      for (const m of individualCollections) {
        fitting = f.startsWith(m.trim().toUpperCase());
        if (fitting) {
          break;
        }
      }
      this.filterList[f] = fitting;
    }
  }

  addPrintManifestationToFilterlist(printManifestation: PrintManifestation) {
    if (this.filterList[printManifestation.titleID] !== true) {
      this.filterList[printManifestation.titleID] = true;
      this.selectedPrintManifestations.push(printManifestation);
      this.filteredManifestations[printManifestation.titleID] = printManifestation;
      this.statsManifestations.set(printManifestation.titleID, 0);

      printManifestation.collections.forEach(collection => {
        if (!this.uniqueCollectionsSet.has(collection)) {
          this.uniqueCollectionsSet.add(collection);
          this.filterList[collection] = (collection !== '???');
          this.statsCollection.set(collection, 0);
        }
      });

      printManifestation.materials.forEach(material => {
        if (!this.uniqueMaterialsSet.has(material)) {
          this.uniqueMaterialsSet.add(material);
          this.statsMaterials.set(material, 0);
          this.filterList[material] = true;
        }
      });

      const filteredItemsInd: Item[] = [];
      const filteredEventsInd: Event[] = [];
      for (const item of printManifestation.items) {
        if (this.filterList[item.collection]) {
          filteredItemsInd.push(item);
          this.selectedItems.push(item);
          if (item.deletionDate === '') {
            let numberCollections = this.statsCollection.get(item.collection);
            numberCollections++;
            this.statsCollection.set(item.collection, numberCollections);
            let numberMaterials = this.statsMaterials.get(item.material);
            numberMaterials++;
            this.statsMaterials.set(item.material, numberMaterials);
            let numberManifestations = this.statsManifestations.get(printManifestation.titleID);
            numberManifestations++;
            this.statsManifestations.set(printManifestation.titleID, numberManifestations);
          }
          for (const event of item.events) {
            if (!(event.type === 'inventory' || event.type === 'deletion') && event.borrowerStatus === '12') {
              continue;
            }
            filteredEventsInd.push(event);
            this.selectedEvents.push(event);
            if (event.endEvent != null) {
              this.selectedEvents.push(event.endEvent);
            }
          }
        }
      }
      this.filteredEvents[printManifestation.titleID] = filteredEventsInd;
      this.filteredItems[printManifestation.titleID] = filteredItemsInd;
      this.uniqueMaterials = Array.from(this.uniqueMaterialsSet).sort();
      this.uniqueCollections = Array.from(this.uniqueCollectionsSet).sort();
      this.selectedEvents.sort(function (firstEvent, secondEvent) {
        return firstEvent.time === secondEvent.time ? 0 : +(firstEvent.time > secondEvent.time) || -1;
      });
    }
  }

  addDigitalManifestationToFilterlist(digitalManifestation: DigitalManifestation) {
    this.selectedDigitalManifestations.push(digitalManifestation);
    this.selectedDigitalUsage.set(digitalManifestation.identifier, digitalManifestation.usage);
    this.filterList[digitalManifestation.identifier] = true;
  }

  applyPrintFilter(printManifestations: Map<string, PrintManifestation>) {
    this.resetPrintSelection();
    this.uniqueCollections.forEach(
      entry => this.statsCollection.set(entry, 0)
    );
    this.uniqueMaterials.forEach(
      entry => this.statsMaterials.set(entry, 0)
    );
    printManifestations.forEach(
      printManifestation => {
        this.statsManifestations.set(printManifestation.titleID, 0);
        const filteredItemsInd: Item[] = [];
        const filteredEventsInd: Event[] = [];
        if (this.filterList[printManifestation.titleID]) {
          this.selectedPrintManifestations.push(printManifestation);

          for (const item of printManifestation.items) {
            if (this.filterList[item.collection] && this.filterList[item.material]) {
              this.selectedCollections.add(item.collection);
              this.selectedShelfmarks.add(item.shelfmark);
              this.selectedItems.push(item);
              filteredItemsInd.push(item);
              if (item.deletionDate === '') {
                let numberCollections = this.statsCollection.get(item.collection);
                numberCollections++;
                this.statsCollection.set(item.collection, numberCollections);

                let numberMaterials = this.statsMaterials.get(item.material);
                numberMaterials++;
                this.statsMaterials.set(item.material, numberMaterials);

                let numberManifestations = this.statsManifestations.get(printManifestation.titleID);
                numberManifestations++;
                this.statsManifestations.set(printManifestation.titleID, numberManifestations);
              }
              for (const event of item.events) {
                if (!(event.type === 'inventory' || event.type === 'deletion') && event.borrowerStatus === '12') {
                  continue;
                }
                filteredEventsInd.push(event);
                this.selectedEvents.push(event);
                if (event.endEvent != null) {
                  this.selectedEvents.push(event.endEvent);
                }
              }
            }
          }
          this.filteredManifestations[printManifestation.titleID] = printManifestation;
          this.filteredEvents[printManifestation.titleID] = filteredEventsInd;
          this.filteredItems[printManifestation.titleID] = filteredItemsInd;
        }
      }
    );
    this.selectedEvents.sort(function (firstEvent, secondEvent) {
      return firstEvent.time === secondEvent.time ? 0 : +(firstEvent.time > secondEvent.time) || -1;
    });
    this.filterChanged$.emit('filterChanged');
  }

  applyDigitalFilter(digitalManifestations: Map<string, DigitalManifestation>) {
    this.resetDigitalSelection();
    digitalManifestations.forEach(
      digitalManifestation => {
        if (this.filterList[digitalManifestation.identifier]) {
          this.selectedDigitalManifestations.push(digitalManifestation);
          this.selectedDigitalUsage.set(digitalManifestation.identifier, digitalManifestation.usage);
        }
      }
    );
  }

  getFilteredTitleIds(): string {
    let titleIds = '';
    this.filteredManifestations.forEach(
      (key, value) => titleIds = titleIds + key + '; '
    );
    titleIds = titleIds.substring(0, titleIds.length - 2 );
    return titleIds;

    }

  getFilteredShelfmarks(): string {
    let shelfmarks = '';
    this.selectedShelfmarks.forEach(
      (shelfmark) => shelfmarks = shelfmarks + shelfmark + '; '
    );
    shelfmarks = shelfmarks.substring(0, shelfmarks.length - 2 );
    return shelfmarks;
  }
}
