import {Component, OnInit} from '@angular/core';
import {MemorylistService} from './service/memorylist.service';
import {AuthenticationService} from './service/authentication.service';
import {MenuItem} from 'primeng/api';
import {Memorylist} from './model/Memorylist';
import {Stockcontrol} from './model/Stockcontrol';
import {FilterService} from './service/filter.service';
import {GetterService} from './service/getter.service';

@Component({
  selector: 'app-stockmanagement',
  templateUrl: 'stockmanagement.component.html'
})

export class StockmanagementComponent implements OnInit {

  public show = {};

  mainMenu: MenuItem[];

  public stockcontrol: Stockcontrol;

  public itemlistMenu: MenuItem[] = [
    {
      label: 'new List',
      icon: 'pi pi-fw pi-plus',
      command: event => this.createNewList()
    }
  ];

  constructor(public itemlistsService: MemorylistService,
              public authenticationService: AuthenticationService,
              public filterService: FilterService) {
  }

  ngOnInit(): void {
    this.show['newList'] = false;
    this.show['uploadForm'] = false;
    this.show['importStockcontrol'] = false;
    if (this.authenticationService.principal) {
      if (this.authenticationService.principal.roles.indexOf('ROLE_FACHREFERENT') > -1) {
        this.getItemlists();
      }
    }
    this.mainMenu = [
      {
        label: 'Listen',
        items: this.itemlistMenu
      },
      {
        label: 'Profil-Import',
        command: event => this.getStockcontrols()
      },
      {
        label: 'csv-Import',
        command: event => this.show['uploadForm'] = true
      },
      {
        label: 'ELi:SA-Daten ndern',
        command: event => window.location.href = '/fachref/user'
      },
      {
        label: 'Listen verwalten',
        command: event => window.location.href = '/fachref/lists'
      },
      {
        label: 'Aufgaben abschicken',
        command: event => window.location.href = '/fachref/lists/' + this.itemlistsService.activeMemorylist.id
      },
      {
        label: 'Logout',
        command: event => this.authenticationService.logout()
      }
    ];
  }

  getItemlists() {
    this.itemlistsService.getItemlistsForUser(this.authenticationService.principal.name).subscribe(
      data => {
        console.log(data);
        this.itemlistsService.memorylists = data;
        data.forEach(entry => this.itemlistMenu.push({
          id: entry.id,
          label: entry.name,
          command: event => {
            this.loadItemlist(entry.id);
          }
        }));
      }
    );
  }

  getStockcontrols() {
    this.itemlistsService.getStockcontrolsForUser(this.authenticationService.principal.name).subscribe(
      data => {
        this.itemlistsService.stockcontrols = data;
        this.show['stockcontrolImport'] = true;
      }
    );
  }

  loadItemlistFromStockcontrol(identifier: string) {
    this.itemlistsService.importStockcontrol(identifier).subscribe(
      data => {
        this.itemlistsService.activeMemorylist = data;
        this.show['stockcontrolImport'] = false;
      }
    );
  }


  loadItemlist(identifier: string) {
    this.itemlistsService.getItemlist(identifier).subscribe(
      data => {
        this.itemlistsService.activeMemorylist = data;
        this.itemlistsService.getItemsForMemorylist(data.id).subscribe(
          savedItems => {
            this.itemlistsService.memorylistItems = savedItems;
            this.itemlistsService.selectMemorylistItem(savedItems[0]);
          }
        );
      }
    );
  }

  createNewList() {
    this.itemlistsService.activeMemorylist = new Memorylist(new Date(), new Date(), 'neue Liste', [], 'Beschreibung');
    this.show['newList'] = true;
  }

  saveNewList() {
    this.itemlistsService.createNewList().subscribe(
      data => {
        this.itemlistsService.activeMemorylist = data;
        this.itemlistsService.memorylists.push(data);
        this.show['newList'] = false;
        this.itemlistMenu.push({
          id: data.id,
          label: data.description,
          command: event => {
            this.loadItemlist(data.id);
          }
        });
      });
  }

  addAction() {
  }
}
