import {EventEmitter, Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import * as appGlobals from '../app.globals';
import {Memorylist} from '../model/Memorylist';
import {Observable} from 'rxjs';
import {MemoryListItem} from '../model/MemoryListItem';
import {Stockcontrol} from '../model/Stockcontrol';
import {Action} from '../model/Action';
import {ItemAction} from '../model/ItemAction';
import {TitleAction} from '../model/TitleAction';

@Injectable()
export class MemorylistService {

  public memorylists: Memorylist[] = [];

  public tabindex = 0;

  public memorylistItems: MemoryListItem[];

  public actionTypes = ['Aussonderung', 'Umstellung'];

  public actionType: string;

  public collectionInd: string;

  public number: number;

  public activeMemorylistItem: MemoryListItem;

  public savedActions: Map<String, Action>;

  public stockcontrols: Stockcontrol[] = [];

  public memorylistItemChanged$: EventEmitter<MemoryListItem>;

  public activeMemorylist: Memorylist = new Memorylist(new Date(), new Date(), '', [], '');

  constructor(private http: HttpClient) {
    this.memorylistItemChanged$ = new EventEmitter();
    this.reset();
  }

  reset() {
    this.stockcontrols = [];
    this.memorylistItems = [];
    this.memorylists = [];
    this.savedActions = new Map<string, Action>();
  }

  createNewList(): Observable<Memorylist> {
    const url = appGlobals.stockanalyzerUrl + '/newMemorylist';
    return this.http.post<Memorylist>(url, JSON.stringify(this.activeMemorylist), {headers: appGlobals.headersJson});
  }

  addMemorylistItem(memorylistItem: MemoryListItem) {
    this.http.post <MemoryListItem>(appGlobals.stockanalyzerUrl + '/memorylistItem',
      JSON.stringify(memorylistItem),
      {headers: appGlobals.headersJson}).subscribe(
      data => {
        this.http.put(appGlobals.stockanalyzerUrl + '/memorylistItem/' + data.id + '/memorylist',
          appGlobals.stockanalyzerUrl + '/memorylist/' + this.activeMemorylist.id,
          {headers: appGlobals.headersConnect}).map(
          response => response['_embedded']['memeorylistItem']).subscribe(
          () => this.activeMemorylistItem = data
        );
      }
    );
  }

  updateItemAction(itemAction: ItemAction): Observable<ItemAction> {
    return this.http.put<ItemAction>(appGlobals.stockanalyzerUrl + '/itemAction/' + itemAction.uuid,
      JSON.stringify(itemAction), {headers: appGlobals.headersJson});
  }

  addItemAction(itemAction: ItemAction): boolean {
    if (this.savedActions.has(itemAction.itemId)) {
      return false;
    } else {
      this.http.post<ItemAction>(appGlobals.stockanalyzerUrl + '/itemAction',
        JSON.stringify(itemAction),
        {headers: appGlobals.headersJson}).subscribe(
        data => {
          this.http.put(appGlobals.stockanalyzerUrl + '/itemAction/' + data.uuid + '/memorylistItem',
            appGlobals.stockanalyzerUrl + '/memorylistItem/' + this.activeMemorylistItem.id,
            {headers: appGlobals.headersConnect}
          ).subscribe(
            () => {
              this.activeMemorylistItem.itemActions.push(data);
              this.savedActions.set(data.itemId, data);
              return true;
            }
          );
        }
      );
    }
  }

  removeItemAction(itemAction: ItemAction): boolean {
    if (!this.savedActions.has(itemAction.itemId)) {
      return false;
    } else {
      this.http.delete(appGlobals.stockanalyzerUrl + '/itemAction/' + itemAction.uuid).subscribe(
        () => {
          this.activeMemorylistItem.itemActions = this.activeMemorylistItem.itemActions.filter(
            entry => entry !== itemAction
          );

          this.savedActions.delete(itemAction.itemId);
        }
      );
    }
  }

  addTitleAction(titleAction: TitleAction): boolean {
    if (this.savedActions.has(titleAction.titleId)) {
      return false;
    } else {
      this.http.post<TitleAction>(appGlobals.stockanalyzerUrl + '/titleAction',
        JSON.stringify(titleAction),
        {headers: appGlobals.headersJson}
      ).subscribe(
        data => {
          this.http.put(appGlobals.stockanalyzerUrl + '/titleAction/' + data.uuid + '/memorylistItem',
            appGlobals.stockanalyzerUrl + '/memorylistItem/' + this.activeMemorylistItem.id,
            {headers: appGlobals.headersConnect}
          ).subscribe(
            () => {
              this.activeMemorylistItem.titleActions.push(data);
              this.savedActions.set(data.titleId, data);
              return true;
            }
          );
        }
      );
    }
  }

  removeTitleAction(titleAction: TitleAction): boolean {
    const key = titleAction.actionType + '-' + titleAction.titleId;
    if (!this.savedActions.has(key)) {
      return false;
    } else {
      this.http.delete(appGlobals.stockanalyzerUrl + '/titleAction/' + titleAction.uuid).subscribe(
        () => {
          this.activeMemorylistItem.titleActions = this.activeMemorylistItem.titleActions.filter(
            entry => entry !== titleAction
          );

          this.savedActions.delete(titleAction.titleId);
        }
      );
    }
  }

  getItemlistsForUser(username: string): Observable<Memorylist[]> {
    const url = appGlobals.stockanalyzerUrl + '/memorylist/forUser/' + username;
    return this.http.get<Memorylist[]>(url);
  }

  getStockcontrolsForUser(username: string): Observable<Stockcontrol[]> {
    const url = appGlobals.stockanalyzerUrl + '/stockcontrol/peruser/' + username;
    return this.http.get<Stockcontrol[]>(url);
  }

  getItemlist(identifier: string) {
    return this.http.get<Memorylist>(appGlobals.stockanalyzerUrl + '/memorylist/' + identifier);
  }

  importStockcontrol(identifier: string): Observable<Memorylist> {
    return this.http.get<Memorylist>(appGlobals.stockanalyzerUrl + '/memorylist/fromStockcontrol/' + identifier);
  }

  getItemsForMemorylist(uuid: string): Observable<MemoryListItem[]> {
    return this.http.get<MemoryListItem[]>(appGlobals.stockanalyzerUrl + '/memorylist/' + uuid + '/memorylistItems')
      .map(response => response['_embedded']['memorylistItem']);
  }

  saveActiveMemorylist(): Observable<Memorylist> {
    return this.http.post<Memorylist>(appGlobals.stockanalyzerUrl + '/memorylist/' + this.activeMemorylist.id,
      JSON.stringify(this.activeMemorylist), {headers: appGlobals.headersJson});
  }

  selectMemorylistItem(savedItem: MemoryListItem) {
    this.activeMemorylistItem = savedItem;
    this.http.get<TitleAction[]>(appGlobals.stockanalyzerUrl + '/memorylistItem/' + this.activeMemorylistItem.id +
      '/titleActions').map(response => response['_embedded']['titleAction']).subscribe(
      data => {
        this.activeMemorylistItem.titleActions = data;
        data.forEach(entry => this.savedActions.set(entry.titleId, entry));
      }
    );
    this.http.get<ItemAction[]>(appGlobals.stockanalyzerUrl + '/memorylistItem/' + this.activeMemorylistItem.id +
      '/itemActions').map(response => response['_embedded']['itemAction']).subscribe(
      data => {
        this.activeMemorylistItem.itemActions = data;
        data.forEach(
          entry => this.savedActions.set(entry.itemId, entry));
      }
    );
    this.memorylistItemChanged$.emit(savedItem);
  }

  nextMemorylistItem() {
    this.tabindex++;
    this.selectMemorylistItem(this.memorylistItems[this.tabindex]);
  }

  previousMemorylistItem() {
    this.tabindex--;
    this.selectMemorylistItem(this.memorylistItems[this.tabindex]);
  }
}
