import {Component, EventEmitter, OnInit} from '@angular/core';
import {GetterService} from './service/getter.service';
import {FilterService} from './service/filter.service';
import {DataService} from './service/data.service';

@Component({
  selector: 'app-filter',
  templateUrl: 'filter.component.html'
})
export class FilterComponent implements OnInit {


  constructor(public getterService: GetterService,
              public filterService: FilterService) {

  }

  ngOnInit(): void {
  }

  toggleShow(feature: string) {
    this.filterService.show[feature] = !this.filterService.show[feature];
  }

  sendEvent() {
    this.filterService.applyPrintFilter(this.getterService.printManifestations);
  }
}
